import React from 'react'; 
import {
  BrowserRouter as Router,
  Route,
} from 'react-router-dom';
import './App.css';
import ListerPage from './pages/ListerPage';
import DevicePage from './pages/DevicePage';

function App() {
  return (
    <Router>
    <div className="App">
      <Route path="/" component={ListerPage} exact/>
      <Route path="/device/:name" component={DevicePage} exact/>
    </div>
    </Router>
  );
}

export default App;
