import React from 'react';
import devices from './device-content'

const DevicePage = ({match}) => {
    const name = match.params.name;
    const mobileDetails = devices.find(device => device.name === name)
    return(
        <>
       <p>This is {name}{mobileDetails.description}</p> 
        </>
    );
     
};
export default DevicePage;