import React from 'react';
import {Link} from 'react-router-dom'

const DeviceTile = (props) => (
    <Link to={`/device/${props.device.name}`}>
    <div className=""> 
        <img alt={props.device.name} className="" height="400" widht="300" src={props.device.imgUrl}/> 
        <p className="my-3">{props.device.name}</p>
        <p>₹{props.device.price}</p>
        <button type="button"  class="btn btn-primary">Learn More</button>
        </div>
        </Link>
);
export default DeviceTile;